<template>
  <v-app>
    <v-main>
      <v-app-bar
        color="negcovid"
        dense
        dark
        shrink-on-scroll
        scroll-target="#scrolling-techniques-2"
        :height="height"
      >
        <v-toolbar-title>
          <div
            style="font-size: 30px; font-weight: bold;"
            v-bind:class="[$vuetify.breakpoint.name === 'xs' ? xsClass : otherClass]"
          >
            Dashboard
          </div>
        </v-toolbar-title>

        <v-spacer></v-spacer>
        <v-row class="pt-5 px-2 ">
          <v-col
            cols="12"
            class="pl-0 ml-0"
          >
            <v-row class="d-flex align-center justify-end">
              <div
                v-if="$vuetify.breakpoint.name !== 'xs'"
                class="pr-5"
                style="font-size: 17px"
              >
                {{this.profile.email}}
              </div>
              <v-avatar
                size="50"
                class="stokeImgMe"
              >
                <v-img :src="this.profile.profilepicture">
                </v-img>
              </v-avatar>
            </v-row>

          </v-col>
          <v-col
            class="pa-0 ma-0"
            v-if="$vuetify.breakpoint.name === 'xs'"
          >
            <v-row
              justify="end"
              class="px-3 py-3"
            >
              <div style="font-size: 17px">
                {{this.profile.email}}
              </div>
            </v-row>
          </v-col>
        </v-row>
      </v-app-bar>

      <!-- <v-sheet
        v-if="$vuetify.breakpoint.name === 'xs'"
        id="scrolling-techniques-2"
        class="overflow-y-auto"
        :max-height="windowHeight - 100"
      >
        <router-view />
      </v-sheet> -->
      <router-view v-if="$vuetify.breakpoint.name === 'xs' || $vuetify.breakpoint.name === 'sm'" />
      <Maindesktop v-if="$vuetify.breakpoint.name === 'md' || $vuetify.breakpoint.name === 'lg'" />
    </v-main>
  </v-app>
</template>

<script>
import Maindesktop from "./views/main_desktop.vue";
export default {
  components: {
    Maindesktop,
  },
  data() {
    return {
      windowHeight: window.innerHeight,
      drawer: false,
      txt: "",
      profile: "",
      xsClass: "pl-0 ",
      otherClass: "pl-10",
    };
  },
  watch: {
    windowHeight(newHeight, oldHeight) {
      this.txt = `it changed to ${newHeight} from ${oldHeight}`;
    },
  },
  async mounted() {
    await this.get_profile();
    this.$nextTick(() => {
      window.addEventListener("resize", this.onResize);
    });
    console.log(this.$vuetify.breakpoint.name);
  },
  methods: {
    onResize() {
      this.windowHeight = window.innerHeight;
    },
    async get_profile() {
      await this.axios
        .get(this.$store.state.path_api + "profile", {
          headers: this.$store.state.path_header,
        })
        .then((result) => {
          this.profile = result.data.result.profile.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  computed: {
    height() {
      if (this.$vuetify.breakpoint.name == "xs") {
        return 90;
      }
      return 70;
    },
  },
};
</script>

<style scoped>
.stokeImgMe {
  border: 2px solid #fff;
}
</style>
